import React, { Component } from 'react';
import { timeConversion } from './App';
import { GARAAŽ_ON, EESRUUM_ON, LEILIRUUM_ON, VALJAS_ON } from './config';


export const Table = (props) => {
    // style={{'marginLeft':'auto','marginRight':'auto'}}
    //console.log(props.data);
  
        return ( 
          <table  className={'table table-condensed'} style={{fontSize: '12px'}} >
            <tbody>
            <TableRow data={props.data} onLegendClick={props.onLegendClick}/>
            </tbody>
          </table>
        );
      }
   
 
const TableRow = (props) => {

    const { data } = props;
    //console.log(data.appProps);
    const leiliruum = data.appProps.leiliruum;
    const eesruum = data.appProps.eesruum;
    const valjas = data.appProps.valjas;
    const garaaz = data.appProps.garaaz;
    function _onClick (e) {
        //console.log(e.target.innerText);
        const returnValue = {};
        returnValue.title = e.target.innerText;

        props.onLegendClick(returnValue);
    }




function styleFunction (data) {
    let styleConst = {
        cursor: "pointer",
        color:  "blue",  
        
    };

    if (data.roomId === 'Garaaž' ) {
        styleConst.color = GARAAŽ_ON;
        if (garaaz) styleConst.opacity = 1; else styleConst.opacity = 0.5;

    } else if (data.roomId === 'Eesruum') {
        styleConst.color = EESRUUM_ON;
        if (eesruum) styleConst.opacity = 1; else styleConst.opacity = 0.5;
    } else if (data.roomId === 'Leiliruum') {
        styleConst.color = LEILIRUUM_ON;
        if (leiliruum) styleConst.opacity = 1; else styleConst.opacity = 0.5;
    } else if (data.roomId === 'Õues') {
        styleConst.color = VALJAS_ON;
        if (valjas) styleConst.opacity = 1; else styleConst.opacity = 0.5;
    } else {
        styleConst.color = "black";
    }
   // console.log(data);
    return styleConst;
}



    const row = data.map((data) =>
  
    <tr  key={data.Id}>
        <td 
            className='text-right font-weight-bold'
            style={styleFunction(data)}

            key={data.roomId} 
            onClick={_onClick}
            >{data.roomId}
        </td>
      <td key={data.temp}>{data.temp} &#176;C</td>
      <td >{(data.roomId === 'Garaaž' || 
        data.roomId === 'Eesruum' || 
        data.roomId === 'Test')?
        data.heat? 'Heater On:' : 'LHP:' : ''} 
      </td>
      <td>{(data.roomId === 'Garaaž' || 
        data.roomId === 'Eesruum' || 
        data.roomId === 'Test') ?
        data.heat ? timeConversion(new Date().getTime() - 
        data.start) : data.start ? calcHeatPeriod(data)+' /' +
        timeConversion(new Date().getTime() - data.end)+' Ago' : 'N/A' : ''} 
      </td>
    </tr>
    );
    return (
      <>{row}</>
    );
  }


function calcHeatPeriod(data) {
  //console.log(data);
  if(data.end - data.start > 0) {
    return timeConversion(data.end - data.start);
  } else {
    return '';
  }
} 