class ControlledTabs extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.handleSelect = this.handleSelect.bind(this);

    this.state = {
      key: 1
    };
  }

  handleSelect(key) {
    //alert(`selected ${key}`);
    this.setState({ key });
  }

  render() {
    const leiliruum = this.props.leiliruum;
    const eesruum = this.props.eesruum;
    const valjas = this.props.valjas;
    const garaaz = this.props.garaaz;
    //console.log(garaaz);
    return (
      <Tabs
        activeKey={this.state.key}
        onSelect={this.handleSelect}
        id="temptab"
      >
        <Tab eventKey={1} title="Temps">
          <Row>
            <Col>
            <Graphics leiliruum={leiliruum}
                    eesruum={eesruum}
                    valjas={valjas}
                    garaaz={garaaz}
            />
            </Col>
          </Row>
        </Tab>
        <Tab eventKey={2} title="Heatings">
          <DataTable />
        </Tab>
      </Tabs>
    );
  }
}


function calcHeatPeriod(data) {
  //console.log(data);
  if(data.end - data.start > 0) {
    return timeConversion(data.end - data.start);
  } else {
    return '';
  }
}
class ServerTime extends React.Component {
  constructor(props) {
    super(props);

     this.state = {
        serveruptime: ''
     };
  }

  //AJAX calls
  componentDidMount() {
    //console.log('SERVERUPTIME HAS MOUNTED');


      var that = this;
      let realtimeURL = server+'/sst';
      fetch(realtimeURL)
      .then((response) => {
        response.json()
          .then((data) => {
            that.setState(() => {
              //console.log(new Date(data.node));
              return {
                serveruptime: data.server,
                nodeuptime: data.node
              };
            });
  
  
  
          })
      })
  
  }
  render() {
    //console.log(moment(this.state.serveruptime));
    return (
      <>
        <Clock data={this.state} />
      </>
    );
  }
}

class Clock extends React.Component {
  //const data = this.props.data;
  
  constructor(props) {
    super(props);
    this.state = {
      time: timeConversion((new Date()).getTime() - (new Date(this.props.data.nodeuptime)).getTime()),
      stime: timeConversion((new Date()).getTime() - (new Date(this.props.data.serveruptime)).getTime())
    };
  }
  componentDidMount() {
    this.intervalID = setInterval(
      () => this.tick(),
      1000
    );
  }
  componentWillUnmount() {
    clearInterval(this.intervalID);
  }
  tick() {
    this.setState({
      time: timeConversion((new Date()).getTime() - (new Date(this.props.data.nodeuptime)).getTime()),
      stime: timeConversion((new Date()).getTime() - (new Date(this.props.data.serveruptime)).getTime())
    });
  }
  render() {

    return (
      <table className='table table_condensed' style={{fontSize: '10px', color: 'white'}}>
        <tbody>
          <tr>
            <td className='text-right'>
              Node UpTime:
            </td>
            <td>
              {this.state.time}
            </td>
            <td className='text-right'>
              Server UpTime:
            </td>
            <td>
              {this.state.stime}
            </td>
          </tr>
        </tbody>
      </table>
    );
  }
}


function timeConversion(millisec) {

  var seconds = (millisec / 1000).toFixed(1);

  var minutes = (millisec / (1000 * 60)).toFixed(1);

  var hours = (millisec / (1000 * 60 * 60)).toFixed(1);

  var days = (millisec / (1000 * 60 * 60 * 24)).toFixed(1);

  if (seconds < 60) {
      return seconds + " Sec";
  } else if (minutes < 60) {
      return minutes + " Min";
  } else if (hours < 24) {
      return hours + " Hrs";
  } else {
      return days + " Days"
  }
}
export { timeConversion };
function GetBrowser()
{
  if ( navigator.userAgent.indexOf("Edge") > -1 && navigator.appVersion.indexOf('Edge') > -1 ) {
    return 'Edge';
}
else if( navigator.userAgent.indexOf("Opera") !== -1 || navigator.userAgent.indexOf('OPR') !== -1 )
{
    return 'Opera';
}
else if( navigator.userAgent.indexOf("Chrome") !== -1 )
{
    return 'Chrome';
}
else if( navigator.userAgent.indexOf("Safari") !== -1)
{
    return 'Safari';
}
else if( navigator.userAgent.indexOf("Firefox") !== -1 ) 
{
    return 'Firefox';
}
else if( ( navigator.userAgent.indexOf("MSIE") !== -1 ) || (!!document.documentMode === true ) ) //IF IE > 10
{
    return 'IE';
}  
else 
{
    return 'unknown';
}
}
