import React, { Component } from 'react';
import { Table } from './Table';
import { server } from './App';

export default class Temp extends Component {
    constructor(props) {
      super(props);
      let realtimeURL = server+'/sse';
       this.eventSource = new EventSource(realtimeURL);
  
       this.state = {
          roomTemp: []
       };
    }
    componentDidMount() {
      this.eventSource.onmessage = e => {
        this.updateTempState(JSON.parse(e.data));
        //console.log(e.data);
      }
  
    }
  
    updateTempState(tempState) {
  
      let tempArray = [];
      let tempObject = {};
      let numerator = 0;
      //console.log((tempState['28-000007d4684f'].TestHeatEnd - tempState['28-000007d4684f'].TestHeatStart));
      for(let i in tempState) {
        tempObject = {
            roomId: mapProperty(i), 
            temp: tempState[i].temp, 
            heat: tempState[i].heat, 
            Id: numerator, 
            start: tempState[i].HeatStart, 
            end: tempState[i].HeatEnd
        }
        ++numerator;
        //console.log(tempState[i]);
        tempArray.push(tempObject);
      }
  
      this.setState({roomTemp: tempArray});
    }
  
  
    render () {
      let tempObject = this.state.roomTemp;
      tempObject.appProps=this.props.appProps;
     // console.log('TEMPPROPS', this.props);
  
      //console.log(tempObject);
  /*style={{'marginLeft':'auto','marginRight':'auto'}}*/ 
      return (
        <div>
          <Table data={tempObject} onLegendClick={this.props.onLegendClick}/>
        </div>
      );
    }
  }
  
  function mapProperty(property) {
    var ruum = '';
    switch(property) {
      case '28-0517c3a351ff':
            ruum = 'Garaaž';
            break;
      case '28-0417c2a85dff':
          ruum = 'Eesruum';
            break;
      case '28-0417c28ee3ff':
          ruum = 'Õues';
            break;
      case '28-0417c2da95ff':
          ruum = 'Leiliruum';
        break;
      case '28-000007d4684f':
          ruum = 'Test';
            break;
      default:
        break;
  
    }
    return ruum;
  }