const qualityType = {
    Garaaz: 'Garaaz',
    Test: 'Test',
    Eesruum: 'Eesruum'
  };
//const server = 'http://kilde27.hopto.org:4000';
function periodFormatter(cell, row) {
    return timeConversion(cell);
}
function dateFormatter(cell, row) {
    return new Date(parseInt(cell))
    .toLocaleDateString('et',{"month":"2-digit","day":"2-digit","hour":"2-digit","minute":"2-digit"});
}
export default class DataTable extends Component {
    constructor(props) {
        super(props);
        this.state = {
  
          periods: [],
          sortName: undefined,
          sortOrder: undefined,
          filterValue: undefined,
          total: 0,
          footerData: [[
            {
                label: '',
                columnIndex: 0
            },
            {
                label: 'Total Value',
                columnIndex: 2,
                align: 'right',
                formatter: tableData => this.addTableData(tableData)
            }

        ]]
        }
        this.onSortChange = this.onSortChange.bind(this);
        this.onFilterChange = this.onFilterChange.bind(this);
      }
    onSortChange(sortName, sortOrder) {
    //console.info('onSortChange', arguments);
    this.setState({
        sortName,
        sortOrder
    });
    }
    onFilterChange(filterObj) {
        //console.log(filterObj.room);
        if (filterObj.room === undefined) {
            this.setState({
                filterValue: undefined
            });
        } else {
            this.setState({
                filterValue: filterObj.room.value
            });
        }


        //let result = this.state.footerData[0].findIndex(element => {return element.columnIndex === 2});
        //this.setState({footerData[0][result].label : 'Uus Label'});
        //console.log(this.state);
        return;
    }
    
    addTableData = (tableData) => {
        //this.setState({total: 0});
        let total = 0;
        tableData = this.state.periods;
        //console.log(tableData);

            for (let i = 0, tableDataLen = tableData.length; i < tableDataLen; i++) {
                if(this.state.filterValue === undefined) {
                    total += tableData[i].period;
                }  else if(this.state.filterValue === tableData[i].room )  {     
                    total += tableData[i].period;
                } 

            
        }

        return (
            <strong>Total: {timeConversion(total)}</strong>
        );
    }

    

    fetchPeriod() {
        const that = this;
        fetch(server+'/period/search?limit=1000&page=0')
        .then((response) => {
          response.json()
          .then((data) => {
            //console.log(timeConversion(data[0].Period));
            that.setState(() => {
                //const periods = data;
                //console.log(typeof periods);
                const periods = data.map(period => {
                    return {room: period.Room,
                    start: period.Start,
                    period: period.Period,
                    end: period.End
                    };
                });
            //console.log(periods);
            return {periods: periods}
            });

          })
          })
      }
    componentDidMount() {
        this.fetchPeriod();
        //console.log(data);
    }
    render() {
        const options = {
            sortName: this.state.sortName,
            sortOrder: this.state.sortOrder,
            onSortChange: this.onSortChange,
            onFilterChange: this.onFilterChange,
            noDataText: 'Data query result was empty',
            defaultSortName: 'start',
            defaultSortOrder: 'desc'
          };
        const { footerData } = this.state;
      return (
        <BootstrapTable data={ this.state.periods } 
        options={ options }
        footerData={ footerData }
        footer
        pagination
        >
            <TableHeaderColumn dataField='room' dataSort 
            formatExtraData={ qualityType } 
            filter={ { type: 'SelectFilter', options: qualityType, condition: 'eq', defaultValue: 'Garaaz' }}
             >Room</TableHeaderColumn>
            <TableHeaderColumn dataField='start' 
            isKey={ true } dataSort dataFormat={dateFormatter} >Start</TableHeaderColumn>
            <TableHeaderColumn dataField='period' dataSort dataFormat={periodFormatter}  >Period</TableHeaderColumn>
            <TableHeaderColumn dataField='end' dataSort dataFormat={dateFormatter}>End</TableHeaderColumn>
        </BootstrapTable>
      );
    }
  }