import React, { Component } from 'react';
import { FlexibleWidthXYPlot, LineSeries, 
    VerticalGridLines, HorizontalGridLines,
    XAxis,
    YAxis

  } from 'react-vis';
  //import '../node_modules/bootstrap/dist/css/bootstrap.min.css';

  //import '../node_modules/react-vis/dist/style.css';
  import { server } from './App';
  import Button 
      from 'react-bootstrap/Button';
  import  ButtonToolbar
   from 'react-bootstrap/ButtonToolbar';
import { GARAAŽ_ON, LEILIRUUM_ON, EESRUUM_ON, VALJAS_ON } from './config';
//import { timeConversion } from './App';

//import '../node_modules/react-vis/dist/style.css';


class Graphics extends Component {
    constructor() {
      super();
      this.updateDimensions = this.updateDimensions.bind(this);
      this._leftClickHandler = this._leftClickHandler.bind(this);
      this.state = {

        temperatures: [],
        viewportwith: window.innerWidth,
        limit: 100,
        page: 0
      }
    }
  
    fetchData () {
      var that = this;
      const page = this.state.page;
      const limit = this.state.limit;
      //console.log('Limit before fetch',limit);
      fetch(server+'/temps/search?limit='+limit+'&page='+page)
      .then((response) => {
        response.json()
          .then((data) => {
            that.setState(() => {
              //console.log(data);
              const temperatures = data;
              let tempPoints = [];
              let tempValjas = [];
              let tempEesruum = [];
              let tempLeiliruum = [];
              let mintemp = [];
              /* temperatures.map(temp => { var garaaz = [];
                 garaaz.push(new Date(temp.TimeStamp), temp.Temp_garaaz);
                return tempPoints.push(garaaz);
               });*/
              //create data for graphics
              temperatures.map(temp => {
                let tempObject = {};
                tempObject.x = (new Date(temp.TimeStamp)).getTime();
                tempObject.y = temp.Temp_garaaz;
                return tempPoints.push(tempObject);
              });
              temperatures.map(temp => {
                let tempObject = {};
                tempObject.x = (new Date(temp.TimeStamp)).getTime();
                tempObject.y = temp.Temp_valjas;
                return tempValjas.push(tempObject);
              });
              temperatures.map(temp => {
                let tempObject = {};
                tempObject.x = (new Date(temp.TimeStamp)).getTime();
                tempObject.y = temp.Temp_Leiliruum;
                return tempLeiliruum.push(tempObject);
              });
              temperatures.map(temp => {
                let tempObject = {};
                tempObject.x = (new Date(temp.TimeStamp)).getTime();
                tempObject.y = temp.Temp_Eesruum;
                return tempEesruum.push(tempObject);
              });
              temperatures.map(temp => {
                var timevariable = new Date(temp.TimeStamp).getTime();
                return mintemp.push(timevariable);

              })
              var min = Math.min(...mintemp);
              //console.log('MIN temp', min);
              return {
                temperatures: data,
                garaaz: tempPoints,
                valjas: tempValjas,
                eesruum: tempEesruum,
                leiliruum: tempLeiliruum,
                mintemp: min
              };
            });
  
  
  
          })
      })
  
    }
    _leftClickHandler = () => {

      const tempVariable = new Date() - (1000*60*60*24)*this.state.limit - this.state.page*this.state.limit*(1000*60*60*24);
      
      const nextLeft = Math.abs(this.state.mintemp - tempVariable)/(1000*60*60*24) < 1 ? true : false;
      //console.log('BEFORE', Math.abs(this.state.mintemp - tempVariable)/(1000*60*60*24), nextLeft, tempVariable);

      this.setState({
        page: nextLeft ? this.state.page + 1 : this.state.page,
        disableLeft: nextLeft ? false : true
 
      });
    }
    _rightClickHandler = () => {
      //console.log('BEFORE Right', this.state.page, this.state.valjas.length);
      this.setState({
        page: this.state.page === 0 ? this.state.page : this.state.page - 1
      });
    }
    updateDimensions () {
      const Limit = window.innerWidth < 800? 10 : 100;
      this.setState({viewportwith: window.innerWidth,
        limit: Limit
      });
      //console.log(this.state.limit);
    }
    componentWillMount () {
      this.updateDimensions();
    }
    //AJAX calls
    componentDidMount() {
      console.log('COMPONENT HAS MOUNTED');
      window.addEventListener('resize', this.updateDimensions);
      this.fetchData();

            
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
      if(this.state.limit !== prevState.limit || this.state.page !== prevState.page) {
        this.fetchData();
      }
    }
    componentWillUnmount() {
      window.removeEventListener('resize', this.updateDimensions);
    }
    render() {
      //console.log(this.props.garaaz);
      //this.props indicates legend click state
      const tyhi = [];
      const data = (this.props.garaaz)?this.state.garaaz:tyhi;
      const valjas = (this.props.valjas)?this.state.valjas:tyhi;
      const eesruum = (this.props.eesruum)?this.state.eesruum:tyhi;
      const leiliruum = (this.props.leiliruum)?this.state.leiliruum:tyhi;
      const rightpagination = this.state.page === 0 ? true: false;


      return (
  <div>
        <ButtonToolbar className='justify-content-between'>
          <Button size='sm' disabled={this.state.nextLeft} variant='outline-primary' onClick={this._leftClickHandler} >
            &larr; Previous Page
          </Button>
          
            <Button size='sm' disabled={rightpagination} variant='outline-primary' onClick={this._rightClickHandler} >
              Next Page &rarr;
            </Button>
          
       </ButtonToolbar>
        <FlexibleWidthXYPlot height={400}  margin={{ top: 10, bottom: 80}}>
        <VerticalGridLines />
        <HorizontalGridLines />
        <XAxis title="Aeg" tickFormat={v => `${new Date(v).toLocaleDateString('et',{"month":"2-digit","day":"2-digit","hour":"2-digit","minute":"2-digit"})}`} tickLabelAngle={-45}/>
        <YAxis title="Temp" />

        <LineSeries data={data} color={GARAAŽ_ON} />

        <LineSeries data={leiliruum } color={LEILIRUUM_ON}/>
        
        <LineSeries data={eesruum} color={EESRUUM_ON}/>

        <LineSeries data={valjas} color={VALJAS_ON}/>


        </FlexibleWidthXYPlot>

  </div>



       );
    }
  }

export default Graphics;
  